














import Vue from "vue";
import { mapState, mapActions } from "vuex";
import ScheduleSlotGroup from "./ScheduleSlotGroup.vue";

export default Vue.extend({
  name: "schedule-day",
  props: ["day"],
  data() {
    return {
      width: 0
    };
  },
  mounted() {
    this.updateWidth();
  },
  computed: {
    headStyle(): object {
      return {
        width: this.width + 'px'
      };
    },
    ...mapState(['viewWidth'])
  },
  watch: {
    viewWidth() {
      this.updateWidth();
    }
  },
  methods: {
    updateWidth(): void {
      let element = this.$el as HTMLElement;
      if (element) {
        this.width = element.offsetWidth;
      }
    },
    ...mapActions(["selectDate"])
  },
  components: {
    ScheduleSlotGroup
  }
});
